import React from 'react';
import './App.css';
import {HashRouter as Router,Redirect,Route} from 'react-router-dom';
// import Mytab from './pages/Mytab';
// import { SearchBar, Switch } from 'antd-mobile';
import Home from './pages/Home';

import Cart from './pages/Cart';
import greenTab from './pages/greenTab';
import yellowTab from './pages/yellowTab';
import Information from './pages/Information';
import SearchBarExample from './pages/SearchBar';
import { Provider } from 'react-redux';
import store from './store'
import Login from './pages/Login';
import shoucang from './pages/shoucang'



const App=()=>{
  return (
    <Provider store={store}>
      <Router>
      <Route exact path='/' component={Home} />
      <Route path='/login' component={Login} />
      <Route path='/shoucang' component={shoucang} />
      <Route path='/greenTab' component={greenTab} />
      <Route path='/yellowTab' component={yellowTab} />
      <Route path='/information' component={Information}></Route>
      <Route path='/searchbar' component={SearchBarExample}></Route>
      <Route render={()=><Redirect to='/'/>}/>
      {/* <Mytab/> */}
      </Router>
    
    </Provider>
  )
}

export default App;
