import React, { Component } from 'react';
import { connect } from 'react-redux';
import  product  from '../action/product';

class Details extends Component{
    constructor(props) {
        super(props);
        
    }
    componentDidMount(){
        this.props.dispatch(product());
    }
    
    render(){
        const pro=this.props.datalist.map(item=>(
            <div key={item.id}>
                <img style={{width:"100%",marginTop:"30px"}} onClick={()=>{this.props.history.push('/information');localStorage.setItem('id',item.id)}} src={item.img} />
                <p>{item.title}</p>
            </div>       
        ))
        return(
            <div>
                {pro}
            </div>
        )               
    } 
}

const mapStateToProps = (state) => ({
    datalist: state.homereducer.datalist
})
export default connect(mapStateToProps)(Details);
