import { Button, SearchBar } from 'antd-mobile'
import React, { Component } from 'react'
// import Details from './Detail';
import './home.css'
import Lunbo from './Lunbo';
import Mytab from './Mytab';
import SearchBarExample from './SearchBar';

import { ActivityIndicator } from 'antd-mobile'
import Details from './Detail';


class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'home',
        };
    }
    
    render(){
        return (
            <div>
                <div style={{background:"#ff9705"}}>
                    <i style={{marginLeft:"10px"}} className="iconfont icon-taobao"></i>
                    <button style={{background:"#ff5a08",border:"0px #ff5a08 solid",width:"300px",marginLeft:"20px",marginBottom:"10px",height:"30px",borderRadius:"5px",color:"white"}} onClick={()=>{this.props.history.push('/searchbar')}}><i className='iconfont icon-search'></i>寻找宝贝店铺</button>
                </div>
                <Lunbo/>
                <Details history={this.props.history}/>
                <Mytab/>   
            </div> 
        )
    }   
}

export default Home;