const shoucangreducer = (state={shoucang:[]},action)=>{
    switch(action.type){
        case 'ADD':
            return [...state,action.value]
        case 'DEL':
            let arr = [...state];
            arr.splice(action.index,1);
            return arr;
        default:
            return state;
        
    }
}

export default shoucangreducer;