import React, { Component } from 'react'
import { Carousel } from 'antd-mobile';

class Lunbo extends React.Component {
    state = {
      data: ['1', '2', '3','4','5'],
      imgHeight: 176,
    }
    componentDidMount() {
      // simulate img loading
      setTimeout(() => {
        this.setState({
          data: ['14/86/O1CN01Cf8h5C1CVQh3rmEtC_!!86-0-lubanu', 'i4/45/O1CN01rNyxVF1CCeS74h08l_!!45-0-ppp-picassogw', 'i4/172/O1CN018tFofI1D8oksbLs1A_!!172-0-lubanu','174/O1CN01rwe6lV1D9jXsArAFM_!!174-0-lubanu','181/O1CN019P74nf1DCwJtEAzR3_!!181-0-ppp-picassogw'],
        });
      }, 1000);
    }
    render() {
      return (
          <Carousel
            autoplay={true}
            infinite
            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
            afterChange={index => console.log('slide to', index)}
          >
            {this.state.data.map(val => (
              <a
                // key={val}
                // href="http://www.alipay.com"
                //style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
              >
                <img
                  src={`https://gw.alicdn.com/imgextra/${val}.jpg`}
                  alt=""
                  style={{ width: '100%', verticalAlign: 'top' }}
                  onLoad={() => {
                    // fire window resize event to change height
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto'});
                  }}
                />
              </a>
            ))}
          </Carousel>
      );
    }
  }

  export default Lunbo;