import React, { Component } from 'react'
import { connect } from 'react-redux';
import Mytab from './Mytab'
import Del from '../action/shoucang'


class shoucang extends Component {
    constructor(props) {
        super(props);
    }
    componentDidMount(){
        this.props.dispatch(Del());
    }
    render() {
        return (
            <div>
                {
                    this.props.shoucang.map((item,idx)=>(
                        <li key={idx}>
                            {item}
                            <button
                                onClick={()=>{
                                    this.props.dispatch({
                                        type: "DEL",
                                        index: idx
                                    })
                                }}
                            >删除</button>
                        </li>
                    ))
                   
                }
                <Mytab/>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    shoucang: state.shoucangreducer.shoucang
})
export default connect(mapStateToProps)(shoucang);