import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'redTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        //console.log(localStorage.getItem('userinfo'));
        //alert(username);
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        title="收藏"
                        key="sc"
                        selected={ this.state.selectedTab === 'blueTab' }
                        onPress={ () => {
                            // this.setState({
                            //     selectedTab = 'blueTab'
                            // });
                            if(localStorage.getItem('userinfo')===null){
                                this.props.history.push('/login')
                                
                            }
                            if(localStorage.getItem('userinfo')==='张三'){
                                this.props.history.push('/shoucang')   
                            }
                        }}
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao'></i>
                        }
                        title="订单列表"
                        key="List"
                        selected={ pathname === '/greenTab' }
                        onPress={ () => {
                            this.props.history.push('/greenTab');
                        } }
                        data-seed="logId1"
                    >  
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wodetaobao'></i> }
                        selectedIcon={
                            <i className='iconfont icon-wodetaobao'></i>
                        }
                        title="我的淘宝"
                        key="My"
                        selected={ pathname === '/yellowTab' }
                        onPress={ () => {
                            this.props.history.push('/yellowTab');
                        } }
                        data-seed="logId1"
                    >  
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

export default withRouter(Mytab);
